.backdrop {
  background-color: rgba(174, 198, 165, 0.3);
  position: fixed;

  overflow: scroll;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  transition: opacity 400ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 400ms cubic-bezier(0.4, 0, 0.2, 1);
}
.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.modal-window_entry {
  display: none;
}

.modal-window_entry.show-entry {
  display: flex;
  justify-content: center;
}
.general-modal {
  width: 280px;
  height: 986px;
  background-color: var(--secondary-text-color);
  position: absolute;
  top: 30px;
  border: 2px solid var(--default-color);
  border-radius: 10px;
  padding: 32px 54px;
  @media screen and (min-width: 768px) {
    width: 568px;
    height: 644px;
  }
}
.modal-btn-close {
  background-color: transparent;
  padding: 0;
  position: absolute;
  top: 6px;
  right: 6px;
}
.modal__title {
  font-family: 'Seymour One', sans-serif;
  color: var(--primary-text-color);
  font-size: 20px;
  line-height: 1.4;
  text-align: center;
}
.general-modal__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    justify-content: center;
    column-gap: 32px;
  }
}
.general-modal__link {
  position: relative;
  width: 132px;
  height: 135px;
  margin-top: 83px;
  border-radius: 15px;
  @media screen and (min-width: 768px) {
    margin-top: 103px;
  }
}
.general-modal__img::before {
  content: '';
  position: absolute;
  transform: translateY(-50%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 50%;
  width: 130px;
  height: 130px;
}
.general-modal__img_icecream {
  background-color: var(--secondary-text-color);
  border: 2px solid var(--border-product-color);

  &::before {
    background-image: url(../images/buy-now-modal/ice-1.png);
    z-index: 100;
    @media screen and (min-resolution: 2dppx) {
      background-image: url(../images/buy-now-modal/ice-1@2x.png);
    }
  }
}
.general-modal__img_icecoffee {
  background-color: var(--background-product-color);

  &::before {
    background-image: url(../images/buy-now-modal/ice-2.png);
    z-index: 100;
    @media screen and (min-resolution: 2dppx) {
      background-image: url(../images/buy-now-modal/ice-2@2x.png);
    }
  }
}
.general-modal__img_milkshake {
  background-color: var(--secondary-text-color);
  border: 2px solid var(--background-products-color);

  &::before {
    background-image: url(../images/buy-now-modal/ice-3.png);
    z-index: 100;
    @media screen and (min-resolution: 2dppx) {
      background-image: url(../images/buy-now-modal/ice-3@2x.png);
    }
  }
}
.general-modal__shadow {
  position: relative;
  width: 132px;
  height: 69px;
  border-radius: 0 0 50% 50% / 0 0 100% 100%;

  &_icecream {
    background: linear-gradient(
        0deg,
        rgba(156, 190, 143, 0.7),
        rgba(156, 190, 143, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.34219px 20.0332px rgba(10, 136, 23, 0.25);
  }

  &_iceсoffee {
    background: linear-gradient(
        0deg,
        rgba(248, 210, 78, 0.7),
        rgba(248, 210, 78, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.40397px 20.2649px rgba(153, 114, 23, 0.25);
  }

  &_milkshake {
    background: linear-gradient(
        0deg,
        rgba(213, 117, 63, 0.7),
        rgba(213, 117, 63, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.41333px 20.3px rgba(0, 0, 0, 0.25);
  }
}
.general-modal__text {
  margin: 20px 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  text-transform: uppercase;
}
.general-modal-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 24px 0;
  row-gap: 12px;
  @media screen and (min-width: 768px) {
    margin: 40px 0 32px 0;
    row-gap: 16px;
  }
}

.general-modal-form__input {
  width: 100%;
  height: 32px;
  background-color: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;
  padding-left: 12px;
  padding-right: 12px;
  transition: border-color 300ms cubic-bezier(0.4, 0, 0.2, 1);

  &::placeholder {
    font-size: 12px;
  }

  &:focus {
    border-color: var(--hover-color);
    outline: none;
  }
}

.general-modal-form__textarea {
  width: 100%;
  height: 64px;
  padding: 8px 12px;
  resize: none;
  background-color: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;
  transition: border-color 300ms cubic-bezier(0.4, 0, 0.2, 1);

  &::placeholder {
    font-size: 12px;
  }

  &:focus {
    border-color: var(--hover-color);
    outline: none;
  }
}

.general-modal-form__btn {
  background-color: transparent;
  padding: 0;
  background-color: var(--default-color);
  border-radius: 10px;
  width: 114px;
  height: 42px;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  color: var(--primary-text-color);
  transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1),
    color 300ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    background-color: var(--hover-color);
  }

  &:focus {
    background-color: var(--hover-color);
    color: var(--secondary-text-color);
  }
}

@media screen and (min-width: 768px) {
  .general-modal {
    width: 568px;
    height: 668px;
    padding: 32px 50px;
    display: flex;
    flex-direction: column;
  }

  .modal-btn-close {
    top: 18px;
    right: 18px;
  }

  .modal-btn-close__icon {
    width: 20px;
    height: 20px;
  }

  .general-modal__title {
    font-size: 20px;
    line-height: 1.4;
    word-spacing: 0px;
    margin-bottom: 24px;
    width: 322px;
    text-align: center;
    align-self: center;
  }

  .general-modal__text {
    font-size: 16px;

    line-height: 1.4;
    margin-bottom: 30px;
    word-spacing: -2px;
  }

  .general-modal-form {
    gap: 16px;
    align-self: center;
  }

  .general-modal-form__input {
    width: 368px;
    height: 42px;

    &::placeholder {
      font-size: 14px;
    }
  }

  .general-modal-form__textarea {
    width: 368px;
    height: 84px;

    &::placeholder {
      font-size: 14px;
    }
  }

  .general-modal-form__btn {
    width: 121px;
    font-size: 14px;
  }
}

@media screen and (max-width: 479px) {
  .general-modal {
    max-width: 100%;
    height: auto;
  }
}
