.thumb {
    position: relative;
    max-width: 1200px;
    margin: 0 auto;
}

.nav-link {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: var(--secondary-text-color);
    transition-property: color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        color: var(--default-color);
    }
}

.nav-list {
    display: flex;
    gap: 27px;
    margin-right: auto;
}



.header {
    width: 100%;
    max-width: 1200px;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    z-index: 1;
}

.header-btn {
    margin: 0;
    padding: 12px 24px;
    background-color: var(--default-color);
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    display: flex;
    align-items: center;
    text-align: center;
    transition-property: color, background-color, border-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
        background-color: var(--hover-color);
    }

    &:focus {
        background-color: transparent;
        border-color: #0A8817;
        color: var(--secondary-text-color);
    }
}

.header-btn-icon {
    width: 12px;
    height: 12px;
    margin-left: 7px;
    transition-property: fill;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    .header-btn:focus & {
        fill: var(--secondary-text-color);
    }
}


.hero-container {
    display: flex;
    justify-content: space-between;
}

.hero-title {
    font-family: 'Seymour One';
    font-style: normal;
    font-weight: 400;
    font-size: 38px;
    line-height: 1.26;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--secondary-text-color);
    padding-top: 118px;
    margin-bottom: 28px;
}

.hero-section {
    background-color: var(--background-main-color);
    background-image: url(/src/images/hero/points-desktop.png);
    background-repeat: no-repeat;
    background-position: bottom;
    max-width: 1200px;
    margin: 0 auto;
}

.hero-btn-wrap {
    display: flex;
    align-items: baseline;
    margin-bottom: 42px;
}

.hero-products-btn {
    margin: 0;
    margin-right: 34px;
    padding: 12px 34px;
    border: 2px solid #F8D24E;
    background-color: transparent;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.29;
    display: flex;
    align-items: center;
    text-align: center;
    z-index: 5;
    transition-property: border-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    border-radius: 10px;

    &:hover {
        border-color: #0A8817;
    }
}

.how-made {
    text-decoration: underline;
    color: var(--default-color);
    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
    z-index: 5;
    transition-property: color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
        color: var(--hover-color);
    }

    &:focus {
        color: var(--secondary-text-color);
    }
}

.plate-wrap {
    display: flex;
    align-items: center;
}

.plate-text-wrap {
    max-width: 200px;
}

.plate-text {
    margin-bottom: 32px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.4;
    color: var(--secondary-text-color);
}

.hero-arrow {
    width: 40px;
    height: 16px;
    fill: var(--secondary-text-color);
    transition: width 250ms ease, height 250ms ease;

    &:hover {
        width: 50px;
    }

    &:focus {
        width: 50px;
    }
}

.plate-img {
    margin-right: 14px;
    padding-bottom: 65px;
}

.plate-img{
    display: inline-block;
    transform-origin: center center;
    animation: animate360 4000ms linear 1000ms infinite;
  
  }
  
  @keyframes animate360 {
    0% {
      transform: rotate(0deg);
    }
  
    10% {
      transform: rotate(45deg);
    }
  
    20% {
      transform: rotate(90deg);
    }
  
    30% {
      transform: rotate(135deg);
    }
  
    40% {
      transform: rotate(180deg);
    }
  
    55% {
      transform: rotate(235deg);
    }
  
    70% {
      transform: rotate(280deg);
    }
  
    85% {
      transform: rotate(335deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }

.info-number {
    font-weight: 900;
    font-size: 46px;
    line-height: 1.21;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--primary-text-color);
}

.info-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.21;
    text-align: right;
    color: var(--secondary-text-color);
}

.ice-cream-img {
    position: absolute;
    bottom: -100px;
    right: 0px;
}

.info-hero-list {
    text-align: right;
    margin-top: 126px;
}

.header-menu-btn {
    display: none;
}

.logo-icon {
    width: 60px;
    height: 55px;
}

.hero-menu {
    display: none;
}

.none {
    display: none;
}

