.thumb {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

.nav-link {
  color: var(--secondary-text-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.nav-link:hover, .nav-link:focus {
  color: var(--default-color);
}

.nav-list {
  gap: 27px;
  margin-right: auto;
  display: flex;
}

.header {
  width: 100%;
  max-width: 1200px;
  z-index: 1;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: absolute;
  left: 0;
}

.header-btn {
  background-color: var(--default-color);
  text-align: center;
  align-items: center;
  margin: 0;
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  transition-property: color, background-color, border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header-btn:hover {
  background-color: var(--hover-color);
}

.header-btn:focus {
  color: var(--secondary-text-color);
  background-color: rgba(0, 0, 0, 0);
  border-color: #0a8817;
}

.header-btn-icon {
  width: 12px;
  height: 12px;
  margin-left: 7px;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.header-btn:focus .header-btn-icon {
  fill: var(--secondary-text-color);
}

.hero-container {
  justify-content: space-between;
  display: flex;
}

.hero-title {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--secondary-text-color);
  margin-bottom: 28px;
  padding-top: 118px;
  font-family: Seymour One;
  font-size: 38px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.26;
}

.hero-section {
  background-color: var(--background-main-color);
  max-width: 1200px;
  background-image: url("points-desktop.4014d7b1.png");
  background-position: bottom;
  background-repeat: no-repeat;
  margin: 0 auto;
}

.hero-btn-wrap {
  align-items: baseline;
  margin-bottom: 42px;
  display: flex;
}

.hero-products-btn {
  text-align: center;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  align-items: center;
  margin: 0 34px 0 0;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  transition-property: border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.hero-products-btn:hover {
  border-color: #0a8817;
}

.how-made {
  color: var(--default-color);
  z-index: 5;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
  text-decoration: underline;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.how-made:hover {
  color: var(--hover-color);
}

.how-made:focus {
  color: var(--secondary-text-color);
}

.plate-wrap {
  align-items: center;
  display: flex;
}

.plate-text-wrap {
  max-width: 200px;
}

.plate-text {
  color: var(--secondary-text-color);
  margin-bottom: 32px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4;
}

.hero-arrow {
  width: 40px;
  height: 16px;
  fill: var(--secondary-text-color);
  transition: width .25s, height .25s;
}

.hero-arrow:hover, .hero-arrow:focus {
  width: 50px;
}

.plate-img {
  transform-origin: center;
  margin-right: 14px;
  padding-bottom: 65px;
  animation: animate360 4s linear 1s infinite;
  display: inline-block;
}

@keyframes animate360 {
  0% {
    transform: rotate(0deg);
  }

  10% {
    transform: rotate(45deg);
  }

  20% {
    transform: rotate(90deg);
  }

  30% {
    transform: rotate(135deg);
  }

  40% {
    transform: rotate(180deg);
  }

  55% {
    transform: rotate(235deg);
  }

  70% {
    transform: rotate(280deg);
  }

  85% {
    transform: rotate(335deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

